// Libraries
import { Switch, Route } from "react-router-dom";

// Components
import Home from "./components/Home/Home";
import Header from "./components/Layouts/Header/Header";
import Footer from "./components/Layouts/Footer/Footer";
import ScrollToTop from "./components/utils/ScrollToTop";

// Styles
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.scss";
import Contact from "./components/Layouts/Contact/Contact.jsx";
import WhatsAppButton from "./components/Layouts/WhatsAppButton/WhatsAppButton.jsx";
import Desarrollos from "./components/Desarrollos/Desarrollos.jsx";
import Detalle from "./components/Detalle/Detalle";
import Despliegue from "./components/Desarrollos/Despliegue/Despliegue";
import Prensa from "./components/Prensa/Prensa";
import Constructora from "./components/Constructora/Constructora";

const App = () => {
  return (
    <>
      <Header />
      <ScrollToTop />
      <Switch>
        {/* HOME */}
        <Route exact path="/">
          <Home />
        </Route>
        <Route path="/desarrollos/:category">
          <Desarrollos />
          <Despliegue />
        </Route>
        <Route path="/constructora">
          <Constructora />
        </Route>
        <Route path="/prensa">
          <Prensa />
        </Route>
        {/* <Route path="/desarrollos/:title" element={<Detalle />} /> */}
      </Switch>
      <Contact />
      <Footer />
      <WhatsAppButton />
      {/* <Footer /> */}
    </>
  );
};

export default App;
